@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;900&display=swap);




.dev-project__links {
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;
}

.dev-project__meta-container {
    padding: 125px 30px 1px;
    max-width: 415px;
    
    
}

/* Bootstrap .button Class Overide */
.dev-project__button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #076492;
    padding: 5px;
    border-radius: 5px;
    color: aliceblue;
    font-weight: 600;
    transition: ease .7s;
}

.dev-project__button--half {
    width: 45%;
}

.dev-project__button--full {
    width: 100%;
}

.dev-project__button:hover {
    color: #076492;
    background-color: aliceblue;
    transition: ease .7s;
}


.dev-project__button h5 {
    margin-top: 7px;
    margin-left: 5px;
}

.dev-project__info {
    max-width: 350px;
    margin: 30px 0 15px;
    border-top: 2px solid #2391c8;
    padding-top: 30px;
}

.dev-project__tech {
    border: 1px solid #2391c8;
    color: #89d8ff;
    border-radius: 30px;
    margin-right: 5px;
    padding: 0 10px;
    display: inline-block;
}

.dev-project__title {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 600;
}


@media only screen and (max-width: 439px) {

    .dev-project__links {
     flex-direction: column;
    }

    .dev-project__button--half {
        margin-bottom: 10px;
        width: 100%;
    }
  
  }
.dev-portfolio__section-margin {
    margin-top: 8rem;
}
.design-portfolio__section {
    margin-top: 6rem;
  }


.design-project__container {
    margin-top: -170px;
}

.design-project__meta-container {
    padding-top: 200px;
