  display: block; 
  min-width: none; 
  word-wrap: break-word;
  background: none;
  background-clip: border-box; 
  border: none;
  border-radius: none;
}

.card-header {
  padding: 0;
    margin-bottom: 0;
    background: none;
    border-bottom: none;
}

.card-body {
